.header {
  position: absolute;
  width: 100%;
}

.header__container {
  padding-top: 35px;
  padding-bottom: 35px;

  @include vp-1023 {
    padding-top: 25px;
    padding-bottom: 30px;
  }

  @include vp-767 {
    padding-top: 21px;
    padding-bottom: 20px;
  }
}

.header a {
  font-weight: 500;
  line-height: 20px;

  @include vp-1023 {
    font-size: 14px;
    line-height: 17px;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 22px;
  }
}

.header__navigation {
  position: relative;
  display: flex;
  // flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}
