@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase;
  font-weight: 400;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  background-color: #f9fbfd;
  color: #011c40;
  border: none;
  cursor: pointer;
  text-align: center;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  transition: opacity 0.3s ease;
  user-select: none;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
    outline: 1px solid #2871b2;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
  outline: 1px solid #2871b2;
}

.header {
  position: absolute;
  width: 100%;
}

.header__container {
  padding-top: 35px;
  padding-bottom: 35px;
}
@media (max-width: 1023px) {
  .header__container {
    padding-top: 25px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .header__container {
    padding-top: 21px;
    padding-bottom: 20px;
  }
}

.header a {
  font-weight: 500;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .header a {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .header a {
    font-size: 18px;
    line-height: 22px;
  }
}

.header__navigation {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
}
@media (max-width: 1023px) {
  .navigation__list {
    gap: 22px;
  }
}
@media (max-width: 767px) {
  .navigation__list {
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }
}

.navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .navigation__toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 12px;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
  .navigation__toggle::before {
    content: "";
    position: absolute;
    width: 24px;
    height: 14px;
    background-image: url("../img/svg/burger.svg");
  }
}

.navigation__logo svg {
  display: block;
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .navigation__logo svg {
    display: block;
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .navigation__logo svg {
    display: block;
    width: 42px;
    height: 22px;
  }
}

.navigation__logo {
  display: block;
  align-self: flex-start;
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .navigation__logo {
    width: 38px;
    height: 20px;
  }
}

.navigation__list a {
  display: block;
  box-sizing: border-box;
  padding-top: 5px;
  padding-bottom: 5px;
  border: 1px solid transparent;
  color: #f9fbfd;
}
@media (hover: hover) {
  .navigation__list a:hover:not(.focus-visible) {
    opacity: 0.8;
    color: #2871b2;
    outline: none;
  }
}
.navigation__list a.focus-visible:focus {
  opacity: 0.8;
  color: #2871b2;
  outline: none;
}

@media (max-width: 767px) {
  .navigation__header-list {
    display: none;
  }
}

@media (max-width: 767px) {
  .navigation__header-list a {
    width: 100%;
    color: #011c40;
    padding: 10px 10px 8px 10px;
  }
}
@media (hover: hover) {
  .navigation__header-list a:hover:not(.focus-visible) {
    opacity: 1;
    color: #011c40;
    border-bottom: 1px solid #f9fbfd;
  }
}
.navigation__header-list a.focus-visible:focus {
  opacity: 1;
  color: #011c40;
  border-bottom: 1px solid #f9fbfd;
}

@media (max-width: 767px) {
  .navigation__header-list li {
    margin-bottom: 8px;
  }
}

@media (max-width: 767px) {
  .is-open .navigation__header-list {
    display: block;
    top: -21px;
    left: 21px;
    padding-top: 74px;
    padding-left: 15px;
    position: absolute;
    background-color: #f9fbfd;
    z-index: 2;
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
  }
}

.is-open .navigation__toggle {
  width: 12px;
}
.is-open .navigation__toggle::before, .is-open .navigation__toggle::after {
  z-index: 3;
  position: absolute;
  content: "";
  width: 19px;
  height: 2px;
  background-color: #011c40;
}
.is-open .navigation__toggle::before {
  background-image: none;
  transform: rotate(45deg);
}
.is-open .navigation__toggle::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .is-open .navigation__logo--mobile {
    z-index: 3;
    fill: #011c40;
    margin-left: 38px;
  }
}

.is-open ~ .main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 43, 102, 0.5);
}

.promo {
  position: relative;
  color: #f9fbfd;
  background-color: #2871b2;
  z-index: -2;
}

.promo img {
  position: absolute;
  top: 0;
  object-fit: cover;
  object-position: bottom;
  height: 100%;
  width: 100%;
  z-index: -1;
}
@media (max-width: 1023px) {
  .promo img {
    object-position: center 70%;
  }
}

.promo__container {
  padding-top: 270px;
  padding-bottom: 286px;
}
@media (max-width: 1023px) {
  .promo__container {
    padding-top: 267px;
    padding-bottom: 276px;
  }
}
@media (max-width: 767px) {
  .promo__container {
    padding-top: 176px;
    padding-bottom: 179px;
  }
}

.promo h1 {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-left: 95px;
  margin-bottom: 12px;
}
@media (max-width: 1023px) {
  .promo h1 {
    font-size: 50px;
    line-height: 61px;
    text-shadow: none;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .promo h1 {
    font-size: 36px;
    line-height: 45px;
    margin-bottom: 11px;
  }
}

.promo p {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  margin-left: 95px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .promo p {
    font-size: 18px;
    line-height: 22px;
    text-shadow: none;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .promo p {
    font-size: 16px;
  }
}

.about__container {
  padding-top: 112px;
  padding-bottom: 145px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 40px;
}
@media (max-width: 1023px) {
  .about__container {
    padding-top: 100px;
    padding-bottom: 94px;
    column-gap: 30px;
  }
}
@media (max-width: 767px) {
  .about__container {
    padding-top: 65px;
    padding-bottom: 85px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 44px;
  }
}

.about__description {
  align-self: center;
}

.about h2 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-top: -3px;
  margin-bottom: 49px;
}
@media (max-width: 1023px) {
  .about h2 {
    font-size: 26px;
    line-height: 36px;
    margin-top: -9px;
    margin-bottom: 41px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 24px;
    line-height: 34px;
  }
}

.about p {
  margin: 0;
  padding: 0;
  margin-top: -26px;
  padding-right: 40px;
}
@media (max-width: 1023px) {
  .about p {
    padding-right: 0;
  }
}

.about img {
  background-color: #e3ecf6;
}

.advantages {
  background-image: url("../img/advantages-background.jpg");
  background-color: #e3ecf6;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
