@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/advantages-background-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .advantages {
    background-image: url("../img/advantages-background-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .advantages {
    background-image: url("../img/advantages-background-mobile@2x.jpg");
  }
