.advantages__container {
  padding-top: 110px;
  padding-bottom: 120px;
}
@media (max-width: 1023px) {
  .advantages__container {
    padding-top: 88px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .advantages__container {
    padding-top: 56px;
    padding-bottom: 65px;
  }
}

.advantages h2 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-bottom: 49px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages h2 {
    font-size: 26px;
    line-height: 39px;
    margin-bottom: 51px;
  }
}
@media (max-width: 767px) {
  .advantages h2 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 31px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media (max-width: 1023px) {
  .advantages__list {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
  }
}

.advantages__item {
  padding: 30px 30px 46px 30px;
  background-color: #f9fbfd;
}
@media (max-width: 1023px) {
  .advantages__item {
    padding: 26px 16px 20px 20px;
  }
