@media (max-width: 767px) {
  .advantages__item {
    padding: 25px 15px 21px 20px;
  }
}

.advantages__item svg {
  display: block;
  margin-bottom: 18px;
}
@media (max-width: 1023px) {
  .advantages__item svg {
    height: 26px;
    width: auto;
