.catalog__container {
  padding-top: 84px;
  padding-bottom: 120px;

  @include vp-1023 {
    padding-top: 89px;
    padding-bottom: 100px;
  }

  @include vp-767 {
    padding-top: 55px;
    padding-bottom: 65px;
  }
}

.catalog h2 {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
  margin-bottom: 49px;

  @include vp-1023 {
    font-size: 26px;
    line-height: 39px;
    margin-bottom: 50px;
  }

  @include vp-767 {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 31px;
  }
}


.catalog__list {
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(4, minmax(206px, 471px));
  grid-auto-rows: 1fr;
  justify-content: center;
  gap: 30px;
  padding: 0 15px;

  @include vp-1023 {
    grid-template-columns: repeat(2, minmax(309px, 471px));
  }

  @include vp-767 {
    grid-template-columns: repeat(1, minmax(270px, 471px));
    padding: 0 10px;
  }
}

.catalog__item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 30px 30px 23px 30px;
  min-height: 386px;
  color: $color-alice-blue;
  position: relative;
  cursor: pointer;
  z-index: 0;
  background-color: $color-tangaroa;

  @include vp-1023 {
    padding: 35px 35px 27px 35px;
  }

  @include vp-767 {
    padding: 25px 25px 18px 25px;
  }
}

.catalog__item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
  z-index: -1;
}

.catalog__item-name {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
}

.catalog__description {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  opacity: 0;
  height: 100%;
  padding: 28px 25px 35px 25px;

  @include vp-1023 {
    padding: 29px 35px 33px 35px;
  }

  @include vp-767 {
    padding: 27px 25px 35px 25px;
  }
}

.catalog__description h3 {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 15px;
}

.catalog__description dl {
  margin: 0;
  overflow: hidden scroll;

  &::-webkit-scrollbar {
    width: 0;
  }
}

.catalog__description dd {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 12px;
}

.catalog__description dt {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
}

.catalog__button {
  width: 100%;
  align-self: center;
  margin-top: auto;
}


.catalog__item:hover img {
  display: none;
}

.catalog__item:hover .catalog__description,
.catalog__item:focus-within .catalog__description,
.catalog__item:active .catalog__description {
  background-color: $color-tangaroa;
  opacity: 1;
}

.catalog__item:hover .catalog__item-name,
.catalog__item:focus-within .catalog__item-name,
.catalog__item:active .catalog__item-name {
  display: none;
}
